'use strict';

$(document).ready(function() {
  $(window).scroll(function() {
    if ($(this).scrollTop() > 100) {
      $('#scroll').css('opacity', '1').css('height', '50px');
    } else {
      $('#scroll').css('opacity', '0').css('height', '0');
    }
  });

  $('#scroll').click(function() {
    $('html, body').animate({ scrollTop: 0 }, 600);

    return false;
  });
});
